.contextMenu {
  &.disable {
    display: none;
  }
  :global {
    .mytab-dropdown-menu-root {
      box-shadow: 0 10px 12px rgba(0,0,0,0.5);
      border-radius: 6px;
      padding: 6px 4px;
      background-color: rgba(11,11,11,0.8);
      backdrop-filter: blur(10px);
      user-select: none;
      pointer-events: auto;
    }
    .mytab-dropdown-menu .mytab-dropdown-menu-item {
      padding: 0;
    }
  }
  .dropdownMenu {
    display: block;
    font-size: 12px;
    line-height: 30px;
    color: #fff;
    padding: 0 4px;
    margin: 2px 0;
    width: 152px;
    &.action {
      cursor: pointer;
      transition: background .2s;
      border-radius: 6px;
      &:hover {
        background-color: rgba(255,255,255, 0.1);
      }
    }
    .dropdownMenuInfo {
      display: flex;
      justify-content: space-between;
      padding: 0 4px;
      .dropdownMenuIcon {
        flex-shrink: 0;
      }
      .dropdownMenuName {
        flex: 1;
      }
    }
    .dropdownSubMenu {
      display: flex;
      flex-wrap: wrap;
      .dropdownSubMenuItem {
        font-size: 12px;
        height: 20px;
        line-height: 20px;
        width: 32px;
        background-color: rgba(255,255,255, 0.1);
        border-radius: 8px;
        text-align: center;
        margin: 0 2px 4px;
        cursor: pointer;
        transition: background-color .25s;
        &:hover {
          background-color: rgba(255,255,255, 0.3);
        }
      }
    }
  }
}
