.appWidget {
  pointer-events: auto;
  transition: all 0.3s;
  transform: translateY(0);
  &.appWidget-w-1 {
    grid-column: span 1;
    width: calc(var(--app-size));
  }
  &.appWidget-w-2 {
    grid-column: span 2;
    width: calc(var(--app-size) * 2 + var(--app-gap-y));
  }
  &.appWidget-w-3 {
    grid-column: span 3;
    width: calc(var(--app-size) * 3 + var(--app-gap-y) * 2);
  }
  &.appWidget-w-4 {
    grid-column: span 4;
    width: calc(var(--app-size) * 4 + var(--app-gap-y) * 3);
  }
  &.appWidget-h-1 {
    grid-row: span 1;
    height: calc(var(--app-size));
  }
  &.appWidget-h-2 {
    grid-row: span 2;
    height: calc(var(--app-size) * 2 + var(--app-gap-x));
  }
  &.appWidget-h-3 {
    grid-row: span 3;
    height: calc(var(--app-size) * 3 + var(--app-gap-x) * 2);
  }
  &.appWidget-h-4 {
    grid-row: span 4;
    height: calc(var(--app-size) * 4 + var(--app-gap-x) * 3);
  }
  &:hover {
    transform: translateY(-8px);
    .appAvatar {
      box-shadow: 0 0 10px rgba(0,0,0,0.30);
    }
    .appTitle {
      transform: translateY(4px);
      opacity: 1;
    }
  }
  .appAvatar {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 14px;
    position: relative;
    transition: all 0.3s;
  }
  .appTitle {
    font-size: 12px;
    text-align: center;
    color: #fff;
    text-shadow: 0 0 4px rgba(0,0,0,0.65);
    transform: translateY(-100%);
    opacity: 0;
    transition: all 0.3s;
    margin: 0 -8px;
    user-select: none;
  }
}
