.barWrap {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 5;
  right: 12px;
  display: flex;
  align-items: center;
}

.appsWrap {
  max-width: calc(var(--app-size) * var(--app-number) + var(--app-gap-x) * var(--app-number) + var(--app-wrap-x) * 2);
  margin: 0 auto;
  padding: 0 var(--app-wrap-x);
  height: 100%;
  .appsPanel {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    .appsSwiper {
      width: 100%;
      position: relative;
      transition-duration: .3s;
      transition-property: transform;
      transition-timing-function: cubic-bezier(.4,0,.2,1);
      .appSwiper {
        width: 100%;
        position: relative;
        .appPanel {
          width: 100%;
          position: relative;
          height: 100%;
          overflow-y: auto;
          &::-webkit-scrollbar {
            display: none;
          }
          .appGrid {
            position: absolute;
            width: 100%;
            display: grid;
            grid-template-columns: repeat(auto-fill, var(--app-size));
            grid-template-rows: repeat(auto-fill, var(--app-size));
            grid-auto-flow: dense;
            grid-gap: var(--app-gap-y) var(--app-gap-x);
            justify-content: center;
            padding: 16px 0;
          }
        }
      }
    }
  }
}
