.container {
  width: 100%;
  height: 100%;
  display: flex;
  cursor: pointer;

  .mainPanel {
    background-color: #fff;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    flex-flow: wrap;
    flex-shrink: 0;
    display: flex;
    align-content: center;
    .miniHeader {
      width: 100%;
      line-height: 32px;
      background-color: rgb(255, 77, 79);
      color: #fff;
    }
    &.needSubPanel {
      border-right: 1px solid rgba(0, 0, 0, 0.05);
    }
  }

  .subPanel {
    background-color: rgb(248, 248, 248);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-flow: wrap;
    width: 100%;
    height: 100%;
  }
}

.calendar {
  width: 100%;
  padding: 0 4%;
  .calendarWeek, .calendarDays {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    & > div {
      width: 14.2857%;
      display: flex;
      justify-content: center;
      align-items: center;
      &:nth-child(7n),
      &:nth-child(7n + 1) {
        color: rgb(255, 77, 79);
      }
      span {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        &.curDay {
          color: #fff;
          background-color: rgb(255, 77, 79);
          border-radius: 100%;
        }
      }
    }
  }
  .calendarWeek {
    color: rgb(28, 28, 28);
    font-weight: 600;
  }
  .calendarDays {
    color: rgb(142, 142, 142);
  }
}


.flex {
  display: flex;
}

.row {
  flex-direction: row !important;
}

.column {
  flex-direction: column !important;
}

.jc-center {
  justify-content: center;
}
.jc-evenly {
  justify-content: space-evenly;
}
.jc-between {
  justify-content: space-between;
}

.f12 {
  font-size: 12px;
}

.f16 {
  font-size: 16px;
}

.f24 {
  font-size: 24px;
}

.f36 {
  font-size: 36px;
}

.f48 {
  font-size: 48px;
}

.tc-red {
  color: rgb(255, 77, 79);
}

.tc-gray {
  color: rgb(142, 142, 142);
}
