.appIcon {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16px;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  & > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .appIconTag {
    pointer-events: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-size: 12px;
    text-align: center;
    font-weight: 500;
    padding: 0px 4px;
    white-space: nowrap;
    line-height: initial;
  }
}
